            <td width="12.5%">本期发生额</td>
            <td width="12.5%">本期应扣除金额</td>
            <td width="12.5%">本期实际扣除金额</td>
            <td width="12.5%">期末余额</td>
          </tr>
          <tr>
            <td>1</td>
            <td>2</td>
            <td>3</td>
            <td>4=2+3</td>
            <td>5（5≤1且5≤4）</td>
            <td>6=4-5</td>
          </tr>
          <tr>
            <td style="text-align:left;">13%税率的项目</td>
            <td width="30">1</td>
            <td style="background-color:#d6ebff;"><input name="1_1" tabindex="1" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="1_2" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="1_3" tabindex="2" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="1_4" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="1_5" tabindex="3" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="1_6" readonly value="0.00"/></td>
          </tr>
          <tr>
            <td style="text-align:left;">9%税率的项目</td>
            <td>2</td>
            <td style="background-color:#d6ebff;"><input name="2_1" tabindex="4" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="2_2" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="2_3" tabindex="5" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="2_4" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="2_5" tabindex="6" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="2_6" readonly value="0.00"/></td>
          </tr>
          <tr>
            <td style="text-align:left;">6%税率的项目（不含金融商品转让）</td>
            <td>3</td>
            <td style="background-color:#d6ebff;"><input name="3_1" tabindex="7" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="3_2" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="3_3" tabindex="8" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="3_4" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="3_5" tabindex="9" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="3_6" readonly value="0.00"/></td>
          </tr>
          <tr>
            <td style="text-align:left;">6%税率的金融转让项目</td>
            <td>4</td>
            <td style="background-color:#d6ebff;"><input name="4_1" tabindex="10" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="4_2" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="4_3" tabindex="11" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="4_4" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="4_5" tabindex="12" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="4_6" readonly value="0.00"/></td>
          </tr>
          <tr>
            <td style="text-align:left;">5%征收率的项目</td>
            <td>5</td>
            <td style="background-color:#d6ebff;"><input name="5_1" tabindex="13" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="5_2" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="5_3" tabindex="14" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="5_4" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="5_5" tabindex="15" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="5_6" readonly value="0.00"/></td>
          </tr>
          <tr>
            <td style="text-align:left;">3%征收率的项目</td>
            <td>6</td>
            <td style="background-color:#d6ebff;"><input name="6_1" tabindex="16" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="6_2" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="6_3" tabindex="17" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="6_4" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="6_5" tabindex="18" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="6_6" readonly value="0.00"/></td>
          </tr>
          <tr>
            <td style="text-align:left;">免抵退税的项目</td>
            <td>7</td>
            <td style="background-color:#d6ebff;"><input name="7_1" tabindex="19" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="7_2" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="7_3" tabindex="20" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="7_4" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="7_5" tabindex="21" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="7_6" readonly value="0.00"/></td>
          </tr>
          <tr>
            <td style="text-align:left;">免税的项目</td>
            <td>8</td>
            <td style="background-color:#d6ebff;"><input name="8_1" tabindex="22" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="8_2" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="8_3" tabindex="23" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="8_4" readonly value="0.00"/></td>
            <td style="background-color:#d6ebff;"><input name="8_5" tabindex="24" value="0.00"/></td>
            <td style="background-color:#ccccff;"><input name="8_6" readonly value="0.00"/></td>
          </tr>
        </table>
      </form>
    </div>
    <div style="margin-top: 15px" class="uk-text-center">
      <button @click="saveForm" class="uk-button uk-button-primary"><i class="uk-icon-save"></i> 保存</button>
      <button class="uk-button uk-button-primary" @click="dataRestore()"><i class="uk-icon-close"></i> 删除</button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-file-excel-o"></i> 导出Excel
      </button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-print"></i> 打印</button>
    </div>
  </div>
</template>
<script>
  import common from '../../mixins/common'
  // computed: {
  //   // 使用对象展开运算符将 getter 混入 computed 对象中

  // }
  export default {
    mixins: [common],
    data() {
      return {
        name: 25,  //当前表格的名称编号
        setting: null,
        com: []
      }
    },
    computed: {
      formData() {
        return this.$store.state.tableData[this.name]
      }
    },
    mounted() {
      this.inputChangeEvent()
      this.inputClickEvent()
      this.getSetting()  //获取后台设置数据
    },
    methods: {
      getSetting() {
        if (this.formData) {
          this.recoverFormData()
        }
        //获取后台预设数据
        // axios.get('getSetting').then(res => {})
        //获取预设数据
        this.com = this.$store.state.companyCurrent.gscompany
        this.setting = this.$store.state.companyCurrent.content.page25
        this.initData()
      },
      initData() {
        this.$Loading.start()
        let that = this
        for (let key in this.setting) {
          J('input').each(function () {
            if (J(this).attr('name') === key) {
              J(this).val((that.setting[key] * 1).toFixed(2))
              J(this).trigger('blur')
              return false
            }
          })
        }
        this.$Loading.finish()
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
            }
          })
        })
        this.$Loading.finish()
      },
      fn() {
        this.fn1(4, 8, [2, 3], 1)
        this.fn2(5, 8)
        this.fn1(6, 8, [4, 5], 2)
      },
      //(
      // q  要修改的值列
      // r  要修改多少行
      // seed 每行参与计算的列
      // type 运算类型
      //    {
      //    1加  a+b
      //    2减  a-b
      //    }
      // )
      fn1(q, r, seed, type) {
        for (let i = 1; i <= r; i++) {
          var sum = 0
          for (let j = 0; j < seed.length; j++) {
            let m = seed[j]
            let dom = J('input[name=' + i + '_' + m + ']')
            if (dom.val()) {
              if (!isNaN(dom.val())) {
                if (type == 1) {
                  sum += parseFloat(dom.val())
                } else if (type == 2) {
                  if (j == 0) {
                    sum = parseFloat(dom.val())
                  } else {
                    sum -= parseFloat(dom.val())
                  }
                }
              }
            }
          }
          J('input[name=' + i + '_' + q + ']').val(sum.toFixed(2))
        }
      },
      //(
      // q  要比较的值列
      // r  要修改多少行
      // a<=1且a<=4
      // )
      fn2(q, r) {
        for (let i = 1; i <= r; i++) {
          if (J('input[name=' + i + '_1]').val()) {
            var dom1 = parseFloat(J('input[name=' + i + '_1]').val())
          } else {
            var dom1 = 0
          }
          if (J('input[name=' + i + '_5]').val()) {
            var dom2 = parseFloat(J('input[name=' + i + '_5]').val())
          } else {
            var dom2 = 0
          }
          if (J('input[name=' + i + '_' + q + ']').val()) {
            var val = parseFloat(J('input[name=' + i + '_' + q + ']').val())
          } else {
            var val = 0
          }
          if (val <= dom1 && val <= dom2) {
            J('input[name=' + i + '_' + q + ']').val()
          } else {
            J('input[name=' + i + '_' + q + ']').val('0.00')
          }
        }
      }
    }
  }
</script>
<style scoped>
  table, table tr th, table tr td {
    font-family: '宋体';
    border: 1px solid #333;
  }

  table {
    max-width: 100%;
    min-height: 25px;
    line-height: 25px;
    text-align: center;
    border-collapse: collapse;
    padding: 2px;
  }

  input, select {
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
    padding: 0 2px;
    outline: 0px;
    text-align: right;
  }

  td {
    text-align: center;
    padding: 3px 2px;
  }

</style>



